@mixin des {
  @media (max-width: 992px) {
    @content;
  }
}

@mixin desk {
  @media (max-width: 1200px) {
    @content;
  }
}
@mixin desk2 {
    @media(max-width: 1100px){
        @content;
    }
}
@mixin tab {
  @media (max-width: 768px) {
    @content;
  }
}
@mixin  mobile {
  //   414px
